export default {
	data() {
		return {
			tabList:[{
				id:1,
				name:'我的卡券'
			},{
				id:2,
				name:'历史卡券'
			}],
			currentTab:0,
			showH5: true,
		};
	},
	onShow(){
		if(navigator){
			this.showH5 = true
		}else{
			this.showH5 = false
		}
	},
	methods:{
		clickTab(index){
			this.currentTab = index
		}
	}
}
